<template>
<nav class='navigation'>
  <div class="navigation__inner">
    <section class="navigation__header">
      <div class="navigation__header-inner">
        <div class="navigation__logo-wrapper">
          <img class="navigation__logo" :src="getlogo" alt="logo">
        </div>
        <p class="navigation__title">Initial Index Fund</p>
      </div>
    </section>
    <ul class="navigation__list">
      <li class="navigation__item">
        <icon-investor class='navigation__item-icon'/>
        <router-link to='/customers' class="navigation__item-text">Customers</router-link>
      </li>
      <li class="navigation__item">
        <icon-exchanges class='navigation__item-icon'/>
        <router-link to='/exchanges' class="navigation__item-text">Exchanges</router-link>
      </li>
      <li class="navigation__item">
        <icon-billing class='navigation__item-icon'/>
        <p class="navigation__item-text">Transactions</p>
      </li>
      <li class="navigation__item">
        <icon-legal class='navigation__item-icon'/>
        <p class="navigation__item-text">Legal</p>
      </li>
    </ul>
  </div>
  <section class="navigation__profile">
    <div class="navigation__profile-card">
      <div class="navigation__ava-wrapper">
        <img class="navigation__ava" :src="getAva" alt="avatar">
      </div>
      <p class="navigation__profile-name">{{ getName }}</p>
    </div>
    <div
      class="navigation__popover-wrapper"
      @mouseleave="popover = false">
      <a
        href='#'
        class='navigation__profile-button'
        v-html='"\&\#8226\&\#8226\&\#8226"'
        @mouseover="popover = true">
      </a>
      <transition
        name="custom-classes-transition"
        enter-active-class="animated dur06 pullUp"
        leave-active-class="animated dur03 fadeOut"
        mode="out-in">
      <popover-navigation
        class='navigation__popover'
        @click.native="popover = false"
        v-show="popover"/>
      </transition>
    </div>
  </section>
</nav>
</template>
<script>
import IconInvestor from '@/components/common/icons/IconInvestor.vue';
import IconBilling from '@/components/common/icons/IconBilling.vue';
import IconLegal from '@/components/common/icons/IconLegal.vue';
import IconExchanges from '@/components/common/icons/IconExchanges.vue';
import PopoverNavigation from '@/components/common/PopoverNavigation.vue';
import { mapState } from 'vuex';

export default {
  name: 'Navigation',
  components: {
    IconInvestor,
    IconBilling,
    IconLegal,
    IconExchanges,
    PopoverNavigation,
  },
  data() {
    return {
      profile: {
        // eslint-disable-next-line
        avatar: require('@/assets/images/ava.jpg'),
        // eslint-disable-next-line
        logo: require('@/assets/images/iin-logo.png'),
      },
      popover: false,
    };
  },
  methods: {
    closePopover() {
      this.popover = false;
    },
  },
  computed: {
    ...mapState('profile', {
      getName: state => state.profile.name,
      getAva: state => state.profile.ava,
      getlogo: state => state.profile.fundLogo,
    }),
  },
};
</script>
<style lang="scss">
.navigation {
  @include flex-col(space-between, stretch);
  display: none;
  @media screen and (min-width: $screen-tablet) {
    display: flex;
    background-color: $N13;
    height: 100vh;
    width: 220px;
    padding: 28px 22px 22px 20px;
  }
  @media screen and (min-width: $screen-desktop) {
    width: 240px;
  }
  &__logo-wrapper {
    width: 30px;
    height: 30px;
    margin-right: 12px;
    @media screen and (min-width: $screen-desktop) {
      margin-right: 18px;
    }
  }
  &__logo {
    width: 100%;
    height: auto;
  }
  &__header {
    width: 100%;
    @include flex-row(flex-start, center);
    margin-bottom: 44px;
  }
  &__header-inner {
    @include flex-row(flex-start, center);
  }
  &__title {
    font-size: $H100;
    font-weight: 500;
    @media screen and (min-width: $screen-desktop) {
      font-size: $H550;
      font-weight: 400;
    }
  }
  &__profile {
    @include flex-row(space-between, center);
    position: relative;
  }
  &__profile-card {
    @include flex-row(flex-start, center);
  }
  &__profile-button {
    color: $N9;
    padding-bottom: 4px;
  }
  &__profile-name {
    font-size: $H050;
    font-weight: 600;
    @media screen and (min-width: $screen-desktop) {
      font-size: $H200;
    }
  }
  &__list {
    list-style: none;
  }
  &__item {
    @include flex-row(flex-start, center);
    margin-bottom: 22px;
    cursor: pointer;
    &:hover {
      .navigation {
        @media screen and (min-width: $screen-desktop) {
          &__item-icon {
          fill: $N0;
          }
          &__item-text {
            color: $N0;
          }
        }
      }
    }
  }
  &__item-icon {
    fill: $N6;
    width: 22px;
    height: 22px;
    margin-right: 10px;
    pointer-events: none;
    transition-property: color, fill, opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-in-out;
  }
  &__item-text {
    font-size: $H200;
    color: $N6;
    transition-property: color, fill, opacity;
    transition-duration: 0.2s;
    transition-timing-function: ease-in-out;
  }
  &__ava-wrapper {
    width: 32px;
    height: 32px;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 12px;
    @media screen and (min-width: $screen-desktop) {
      margin-right: 16px;
    }
  }
  &__ava {
    width: 100%;
    height: 100%;
  }
  &__popover-wrapper {
    position: relative;
    padding: 5px 0;
  }
  &__popover {
    position: absolute;
    bottom: 12px;
    right: -14px;
    z-index: 2;
  }
}
</style>
